<script setup lang="ts">
import { Check, Circle } from 'lucide-vue-next'
import type { Step } from '@/types'

const props = defineProps<{
  steps: Step[]
  currentStepId?: string
}>()

const getStepStatus = (step: Step, index: number) => {
  if (props.currentStepId) {
    const currentIndex = props.steps.findIndex(s => s.id === props.currentStepId)
    if (index < currentIndex) return 'completed'
    if (index === currentIndex) return 'active'
    return 'pending'
  }
  return step.status
}
</script>

<template>
  <div class="relative py-4 pl-4">
    <div 
      v-for="(step, index) in steps" 
      :key="step.id"
      class="relative flex gap-6 pb-12 last:pb-0 group"
    >
      <!-- Line -->
      <div 
        v-if="index !== steps.length - 1"
        class="absolute left-[15px] top-8 bottom-0 w-0.5 bg-slate-200 group-last:hidden"
        :class="getStepStatus(step, index) === 'completed' ? 'bg-green-500' : ''"
      ></div>

      <!-- Icon/Dot -->
      <div class="relative z-10 flex-shrink-0">
        <div 
          class="w-8 h-8 rounded-full border-2 flex items-center justify-center transition-all duration-300 bg-white"
          :class="[
            getStepStatus(step, index) === 'completed' 
              ? 'border-green-500 bg-green-50 text-green-600' 
              : getStepStatus(step, index) === 'active'
                ? 'border-blue-600 bg-blue-50 text-blue-600 scale-110 shadow-md'
                : 'border-slate-300 text-slate-300'
          ]"
        >
          <Check v-if="getStepStatus(step, index) === 'completed'" class="w-4 h-4" />
          <div v-else-if="getStepStatus(step, index) === 'active'" class="w-3 h-3 bg-blue-600 rounded-full"></div>
          <Circle v-else class="w-4 h-4" />
        </div>
      </div>

      <!-- Content -->
      <div class="pt-1 flex-1 transition-opacity duration-300" 
           :class="getStepStatus(step, index) === 'pending' ? 'opacity-50' : 'opacity-100'">
        <h3 class="font-bold text-lg text-slate-800">{{ step.title }}</h3>
        <p v-if="step.description" class="text-sm text-slate-500 mt-1">{{ step.description }}</p>
        
        <div class="mt-4">
          <slot :name="`step-${step.id}`"></slot>
        </div>
      </div>
    </div>
  </div>
</template>
